<template>
  <v-dialog
    v-model="isModalOpen"
    persistent
    max-width="600px"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        color="blue darken-4"
        dark
        v-bind="attrs"
        v-on="on"
      >
        <slot />
      </v-btn>
    </template>
    <v-card class="pa-5">
      <v-form
        @submit.prevent="onSubmit"
        ref="form"
        class="custom-form"
      >
        <v-card-title>
          <span class="headline">{{ title }}</span>
        </v-card-title>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-row>
                <v-col cols="4" class="py-0">
                  <label class="custom-form-label" :for="'room-type-title'">Room type title (for management)</label>
                </v-col>
                <v-col cols="8" class="py-0">
                  <v-text-field
                    id="room-type-title"
                    v-model="typeTitle"
                    :rules="rules.titleRequired"
                    dense
                    outlined
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4" class="py-0">
                  <label class="custom-form-label" :for="'room-number'">Number of rooms</label>
                </v-col>
                <v-col cols="8" class="py-0">
                  <v-text-field
                    id="room-number"
                    type="number"
                    v-model="quantity"
                    min="1"
                    :rules="rules.minRooms"
                    dense
                    outlined
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red accent-4"
            dark
            @click="isModalOpen = false"
          >
            Cancel
          </v-btn>
          <v-btn
            color="blue darken-4"
            dark
            type="submit"
          >
            Save and create
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'CreateRoomTypeModal',
  data () {
    return {
      title: 'Creating a new room type',
      isModalOpen: false,
      valid: true,
      quantity: 1,
      typeTitle: '',
      rules: {
        titleRequired: [value => !!value || 'Name is required'],
        minRooms: [value => value > 0 || 'Must be minimum 1 room']
      }
    }
  },
  methods: {
    onSubmit () {
      if (!this.$refs.form.validate()) {
        return
      }
      this.isModalOpen = false
      // it will be changed when backend is ready
      this.$router.push({ path: '/facility/settings/43224/room-types/43323/edit' })
    }
  }
}
</script>

