<template>
  <base-facility>
    <template v-slot:title>Kyoto</template>
    <template v-slot:default>
      <v-card class="v-card-responsive-table pa-5" shaped tile >
        <v-card-title class="grey--text mt-n5" v-once>
<!--          <router-link to="/facility" class="mr-2 grey&#45;&#45;text text-decoration-none">{{ backTableTitle }}</router-link> > name of room's type-->
          {{ backTableTitle }} > Kyoto
        </v-card-title>
        <v-simple-table class="custom-table d-flex mt-n4">
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">
                Room type ID
              </th>
              <th class="text-left">
                Room type Name
              </th>
              <th></th>
              <th class="text-left">
                Room type settings
              </th>
            </tr>
            </thead>
            <tbody>
            <tr
              v-for="item in roomTypes"
              :key="item.id"
            >
              <td>{{ item.id }}</td>
              <td class="font-weight-medium">{{ item.name }}</td>
              <td>
                <v-btn
                  color="primary"
                  small
                  link
                  :to="'room-types/' + item.id + '/rooms'"
                >Each room settings</v-btn>
              </td>
              <td class="text-center">
                <v-btn
                  color="white"
                  elevation="2"
                  x-small
                  fab
                  @click="editType"
                >
                  <v-icon color="primary">
                    mdi-pencil
                  </v-icon>
                </v-btn>
              </td>
            </tr>
            </tbody>
          </template>
        </v-simple-table>
        <div class="text-end mt-5">
          <create-room-type-modal>Create a new room type</create-room-type-modal>
        </div>
      </v-card>
    </template>
  </base-facility>
</template>

<script>
import BaseFacility from '@/components/Facilities/BaseFacility'
import CreateRoomTypeModal from '@/components/Facilities/RoomTypes/CreateModal'

export default {
  name: 'FacilityRoomTypesSettings',
  data () {
    return {
      backTableTitle: 'Facility list',
      roomTypes: []
    }
  },
  mounted () {
    this.initRoomTypes()
  },
  methods: {
    initRoomTypes () {
      for (let i = 0; i < 5; i++) {
        this.roomTypes.push({
          id: Math.floor(Math.random() * 90000) + 10000,
          name: 'Room type №' + (i + 1)
        })
      }
    },
    editType () {
      this.$router.push({ path: '/facility/settings/43224/room-types/43323/edit' })
    }
  },
  components: {
    CreateRoomTypeModal,
    BaseFacility
  }
}
</script>

<style scoped lang="scss">

</style>
